.page-content {
  // .page-content__basket
  &__basket {
    margin-top : 60px;

    @media screen and (max-width : 767px) {
      margin-top : 0;
    }


  }
}

.basket-page {
  // .basket-page__list
  &__list {
  }
}

.basket-list {
  // .basket-list__inner
  &__inner {

  }

  // .basket-list__item
  &__item {
    margin-bottom : 5px;
  }

  // .basket-list__footer
  &__footer {
    margin-top : 35px;

    @media screen and (max-width : 1140px) {
      margin-top : 25px;
    }

  }

  // .basket-list__btn
  &__btn {
    display     : block;
    width       : 286px;
    margin-top  : 30px;
    margin-left : auto;

    @media screen and (max-width : 1140px) {
      margin-right : auto;
    }
  }
}


.item-basket-list {
  font-size       : 14px;
  line-height     : 1.2857;
  position        : relative;
  display         : flex;
  align-items     : center;
  justify-content : space-between;
  box-sizing      : border-box;
  padding         : 5px 0 5px 0;
  border-top      : 1px solid #e5e5e5;

  @media screen and (max-width : 767px) {
    flex-wrap      : wrap;
    padding-top    : 15px;
    padding-bottom : 15px;
    padding-left   : 65px;
  }


  &:last-child {
    border-bottom : 1px solid #e5e5e5;
  }


  // .item-basket-list__wrapper
  &__wrapper {
    display       : flex;
    align-items   : center;
    min-width     : 380px;
    margin-right  : auto;
    padding-right : 15px;


    @media screen and (max-width : 1140px) {
      display               : grid;
      min-width             : 0;
      grid-template-columns : 100px 210px;
      grid-template-rows    : 1fr 1fr;
    }

    @media screen and (max-width : 767px) {
      align-items           : flex-start;
      width                 : calc(100% + 65px);
      margin-left           : -65px;
      grid-template-columns : 80px 100%;
    }


  }


  // .item-basket-list__image
  &__image {
    display         : flex;
    flex            : 0 0 77px;
    justify-content : center;
    width           : 77px;
    max-height      : 74px;
    margin-right    : 22px;

    @media screen and (max-width : 1140px) {
      grid-column : 1 / 2;
      grid-row    : 1 / 3;
      margin-right    : 0;
    }


    img {

      @media screen and (max-width : 767px) {
        max-width : 51px;
      }

    }


  }

  // .item-basket-list__info
  &__info {


  }

  // .item-basket-list__name
  &__name {
    font-weight     : 500;
    display         : block;
    max-width       : 200px;
    margin-right    : 10px;
    text-decoration : none;
    color           : $mainFontColor;

    @media screen and (max-width : 767px) {
      max-width : none;
    }


  }

  // .item-basket-list__props
  &__props {
    margin-top : 3px;

    @media screen and (max-width : 767px) {
      margin-top : 0;
    }
  }

  // .item-basket-list__price
  &__price {
    font-weight : bold;
    margin      : 0 auto;
    padding     : 0 15px;
    white-space : nowrap;

    @media screen and (max-width : 767px) {
      display : none;
    }
  }

  // .item-basket-list__price-total
  &__price-total {
    font-weight : bold;
    text-align  : right;
    white-space : nowrap;


    @media screen and (max-width : 1140px) {
      margin  : 0 auto;
      padding : 0 15px;
    }

    @media screen and (max-width : 560px) {
      width      : 100%;
      margin-top : 20px;
      text-align : left;
    }
  }

  // .item-basket-list__footer
  &__footer {
    flex         : 0 0 auto;
    margin-left  : 60px;
    padding-left : 15px;

    @media screen and (max-width : 1140px) {
      margin-left  : auto;
      padding-left : 15px;
    }


  }

  // .item-basket-list__delete
  &__delete {
    flex : 0 0 19px;

    @media screen and (max-width : 560px) {
      position : absolute;
      right    : 0;
      bottom   : 53px;
    }

    > svg {
      @media screen and (max-width : 767px) {
        max-width : 25px;
      }
    }
  }

  // .item-basket-list__qty
  &__qty {
    margin  : 0 auto;
    padding : 0 15px;

    @media screen and (max-width : 767px) {
      margin-top  : 15px;
      margin-left : 0;
    }
  }
}

.item-basket-list:first-child {

  @mixin basket-table-headers {
    font-style  : normal;
    position    : absolute;
    top         : -30px;
    display     : block;
    font-weight : 500;
    font-size   : 14px;
    line-height : 1.2857;


    @media screen and (max-width : 767px) {
      display : none;
    }
  }


  // .item-basket-list__image
  .item-basket-list__image {
    &::before {
      content : 'Фото';
      @include basket-table-headers;

    }
  }

  // .item-basket-list__info
  .item-basket-list__info {

    &::before {
      content : 'Наименование';
      @include basket-table-headers;
    }
  }

  // .item-basket-list__props
  .item-basket-list__props {
    &::before {
      content : 'Характеристики';
      @include basket-table-headers;

      @media screen and (max-width : 1140px) {
        display : none;
      }
    }
  }


  // .item-basket-list__qty
  .item-basket-list__qty {

    &::before {
      content : 'Количество';
      @include basket-table-headers;
    }
  }

  // .item-basket-list__price
  .item-basket-list__price {

    &::before {
      content : 'Цена, ₽';
      @include basket-table-headers;
    }

  }

  // .item-basket-list__price-total
  .item-basket-list__price-total {

    &::before {
      content : 'Стоимость, ₽';
      @include basket-table-headers;
    }
  }

}


